<style scoped lang="stylus" rel="stylesheet/stylus">
.demo
  width 100%
  min-height 100%
  background #f0f2f5
  .item
    margin 10px 0 0 10px
</style>

<template>
  <div class="demo">
    <van-button
      v-for="(item, index) in list"
      :key="index"
      class="item"
      @click="go(item)">{{item.meta && item.meta.title || item.name}}</van-button>
  </div>
</template>

<script>
import { routes } from '../router.js'
export default {
  name: 'demo',
  components: {
  },
  props: {
  },
  data() {
    return {
      list: routes,
    };
  },
  methods: {
    go(item) {
      this.$router.push(item.path)
    },
  },
};
</script>



